import React from 'react'
import { Col, Button, ButtonGroup } from 'react-bootstrap'
import { Detalles } from '../../../Modelos/General'
import FilaProducto from './FilaProducto'
import AuxiliarNuevo, { ProductosModal } from '../../../Utiles/Prototipos/AuxiliarNuevo'
import CacheStorage from '../../../Utiles/Componentes/CacheStorage'
import { Factura } from '../../../Modelos/Factura'
import { toast } from 'react-toastify'
import { Producto } from '../../../Modelos/Productos'
import CalcularImpuesto from '../../../Utiles/Prototipos/CalcularImpuesto'
export default class AuxiliarFactura extends AuxiliarNuevo {
    productos: ProductosModal = 'Factura';
    addProductos(pr: Detalles) {
        let data = this.props.data as Factura;
        if (data.id_nota !== undefined)
            toast("UNA NOTA DE ENTREGA NO PUEDE SER EDITADA AL FACTURARLA", { type: toast.TYPE.ERROR })
        else super.addProductos(pr)
    }
    removeProductos(pr: Detalles) {
        let data = this.props.data as Factura;
        if (data.id_nota !== undefined)
            toast("UNA NOTA DE ENTREGA NO PUEDE SER EDITADA AL FACTURARLA", { type: toast.TYPE.ERROR })
        else super.removeProductos(pr)
    }
    createProducto(p: Producto) {
        let data = this.props.data as Factura;
        if (data.id_nota !== undefined)
            toast("UNA NOTA DE ENTREGA NO PUEDE SER EDITADA AL FACTURARLA", { type: toast.TYPE.ERROR })
        else super.createProducto(p)
    }
    BotonesLeft() {
        const { data, setData } = this.props;
        return <Col xs={6}>
            <ButtonGroup>
                <Button onClick={() => { setData({ cargarCotizacion: true }) }}
                    data-class="toolbody" data-html="true"
                    data-tip="CARGAR COTIZACION" variant="primary">
                    <span className="fa fa fa-calculator" />
                </Button>
                <Button onClick={() => { setData({ cargarNota: true }) }}
                    data-class="toolbody" data-html="true"
                    data-tip="CARGAR NOTA" variant="primary">
                    <span className="fa fa-send" />
                </Button>
            </ButtonGroup>
            <CacheStorage setData={setData} data={data} Identificador="NuevaFactura" />
        </Col>
    }
    Row({ data }: { data: Detalles }) {
        return <FilaProducto key={'USD' + data.codigo} producto={data} edit={this.addProductos.bind(this)} remove={this.removeProductos.bind(this)} />;
    }

    THead() {
        return <tr className='info'>
            <th style={{ width: "100px" }}>CODIGO</th>
            <th>DESCRIPCI√ìN</th>
            <th style={{ width: "100px", textAlign: 'right' }}>CANTIDAD</th>
            <th style={{ width: "150px", textAlign: 'right' }}>DISPONIBLE</th>
            <th style={{ width: "150px", textAlign: 'right' }}>P.U.</th>
            <th style={{ width: "200px", textAlign: 'right' }}>TOTAL</th>
            <th style={{ width: "110px", textAlign: 'right' }}>ACCIONES</th>
        </tr>
    }
    Calcular({ imponible, exento, impuesto }: { imponible: number, exento: number, impuesto: number }) {
        return <CalcularImpuesto exento={exento} imponible={imponible} cols={3} />;
    }
}