import React, { useContext } from 'react';
import { Col, Row } from 'react-bootstrap';
import { Route, Link } from "react-router-dom"
import Ventas from '../App/Config/Ventas';
import Region from '../App/Config/Region';
import Factura from '../App/Config/Impreciones/Factura';
import Empresa from '../App/Config/Empresa';
import ListDepartamento from '../App/Departamento';
import ListTipo from '../App/Tipos';
import ListUnidad from '../App/Unidad';
import ListMoneda from '../App/Moneda';
import Orden from '../App/Config/Impreciones/Orden';
import Compra from '../App/Config/Impreciones/Compra';
import Cotizacion from '../App/Config/Impreciones/Cotizacion';
import Nota from '../App/Config/Impreciones/Nota';
import Reporte from '../App/Config/Impreciones/Reporte';
import Ajuste from '../App/Config/Impreciones/Ajuste';
import Costos from '../App/Config/Costos';
import Utilidades from '../App/Config/Utilidades';
import ListaRoles from '../App/Rol';
import { Menus, MenuIten } from '../Utiles/Componentes/Menus';
import { SeccionContext } from '../Context/Seccion'

export default function MasterConfig() {
    const context = useContext(SeccionContext);
    let p: string[] = []
    if (context !== null)
        p = context.data.Permisos;
    return (<Row style={{ minHeight: "calc(100vh - 145px)" }}>
        <Col md={3} style={{ minHeight: "calc(100vh - 145px)" }}>
            <Menus title='CONFIGURACIONES GENERALES'>
                <Link className='btn btn-default' to='/config/empresa'>
                    EMPRESA
                </Link>
                <Link className='btn btn-default' to='/config/region'>
                    REGION
                </Link>
                <Link className='btn btn-default' to='/config/ventas'>
                    VENTAS
                </Link>
                <MenuIten p={p} to='/config/moneda' permiso='MONEDA' >
                    MONEDAS
                </MenuIten>
                <MenuIten p={p} to='/config/roles' permiso='USUARIO' >
                    ROLES
                </MenuIten>
            </Menus>
            <Menus title='CONFIGURACIONES DE IMPRESI√ìN'>
                <MenuIten p={p} to='/config/orden' permiso='ORDEN' >
                    ORDEN DE COMPRA
                </MenuIten>
                <MenuIten p={p} to='/config/compra' permiso='COMPRA' >
                    COMPRA
                </MenuIten>
                <MenuIten p={p} to='/config/cotizacion' permiso='COTIZACION' >
                    COTIZACION
                </MenuIten>
                <MenuIten p={p} to='/config/nota' permiso='NOTA' >
                    NOTA DE ENTREGA
                </MenuIten>
                <MenuIten p={p} to='/config/factura' permiso='FACTURA' >
                    FACTURA
                </MenuIten>
                <MenuIten p={p} to='/config/ajustes' permiso='AJUSTE' >
                    AJUSTE DE INVENTARIO
                </MenuIten>
                <MenuIten p={p} to='/config/reportes' permiso='REPORTE' >
                    REPORTES
                </MenuIten>
            </Menus>
            <Menus title='CONFIGURACIONES DE PRODUCTO'>
                <MenuIten p={p} to='/config/departamentos' permiso='DEPARTAMENTO' >
                    DEPARTAMENTOS
                </MenuIten>
                <MenuIten p={p} to='/config/tipo_producto' permiso='TIPO' >
                    TIPO DE PRODUCTO
                </MenuIten>
                <MenuIten p={p} to='/config/unidades' permiso='UNIDAD' >
                    UNIDADES DE MEDIDA
                </MenuIten>
                <MenuIten p={p} to='/config/costos' permiso='PRODUCTO' >
                    MANEJO DE COSTOS
                </MenuIten>
                <MenuIten p={p} to='/config/utilidades' permiso='PRODUCTO' >
                    UTILIDADES DEFAULT
                </MenuIten>
            </Menus>
        </Col>
        <Col md={9} style={{ minHeight: "calc(100vh - 145px)" }}>
            <Route path="/config/ventas" component={Ventas} />
            <Route path="/config/region" component={Region} />
            <Route path="/config/orden" component={Orden} />
            <Route path="/config/compra" component={Compra} />
            <Route path="/config/cotizacion" component={Cotizacion} />
            <Route path="/config/nota" component={Nota} />
            <Route path="/config/factura" component={Factura} />
            <Route path="/config/ajustes" component={Ajuste} />
            <Route path="/config/reportes" component={Reporte} />
            <Route path="/config/empresa" component={Empresa} />
            <Route path="/config/departamentos" component={ListDepartamento} />
            <Route path="/config/tipo_producto" component={ListTipo} />
            <Route path="/config/unidades" component={ListUnidad} />
            <Route path="/config/moneda" component={ListMoneda} />
            <Route path="/config/costos" component={Costos} />
            <Route path="/config/utilidades" component={Utilidades} />
            <Route path="/config/roles" component={ListaRoles} />
        </Col>
    </Row>)
}
