import React from 'react';
import { Switch, Route } from "react-router-dom"
import ListAjustes from '../App/Ajustes';
import NuevaAjuste from '../App/Ajustes/Nuevo';
import ListaProductos from '../App/Productos';
import ProductosProvider from '../Context/Producto';
export default function MasterProductos() {
    return (<ProductosProvider>
        <Switch>
            <Route path="/producto" exact component={ListaProductos} />
            <Route path="/producto/ajuste/lista" component={ListAjustes} />
            <Route path="/producto/ajuste/nuevo" component={NuevaAjuste} />
        </Switch>
    </ProductosProvider>);
}
