import React from 'react';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom"
import 'react-toastify/dist/ReactToastify.css';
import { ToastContainer } from 'react-toastify';
import Header from './Template/Header';
import Menu from './Template/Menu';
import Footer from './Template/Footer';
import ListaUsuarios from './App/Usuarios';
import Panel from './App/Panel';
import Login from './App/Login';
import { SeccionContext } from './Context/Seccion'
import ProductosProvider from './Context/Producto'
import MasterVentas from './Masters/Ventas';
import MasterCompras from './Masters/Compras';
import MasterConfig from './Masters/Config';
import MasterReportes from './Masters/Reporte';
import MasterProductos from './Masters/Productos';
class App extends React.Component {
  render() {
    if (!this.context.data.seccion)
      return <Login update={this.forceUpdate.bind(this)} />
    let data = localStorage.getItem('menu');
    if (!data) { data = "open"; localStorage.setItem('menu', 'open'); }
    let styles: any = { minHeight: "calc(100vh - 150px)", marginRight: '15px', transition: 'width 2s' };
    if (data === 'cerrado') styles.marginLeft = "90px";
    else styles.marginLeft = "270px";
    return (<Router>
      <ProductosProvider>
        <Header update={this.forceUpdate.bind(this)} key='headerBar' />
        <Menu key='MenuAside' />
        <section style={styles} className="content">
          <ToastContainer position='bottom-right' />
          <br />
          <Switch>
            <Route path="/" exact component={Panel} />
            <Route path={["/cotizacion", "/factura", "/nota", "/clientes",]} component={MasterVentas} />
            <Route path={["/orden", "/compra", "/proveedores",]} component={MasterCompras} />
            <Route path='/producto' component={MasterProductos} />
            <Route path="/usuarios" component={ListaUsuarios} />
            <Route path='/config' component={MasterConfig} />
            <Route path='/reporte' component={MasterReportes} />
          </Switch>
        </section>
        <Footer />
      </ProductosProvider>
    </Router >);
  }
  componentDidMount() {
    let body = document.getElementsByTagName('body')[0];
    let data = localStorage.getItem('menu');
    if (!data) {
      data = "open";
      localStorage.setItem('menu', 'open')
    }
    if (body) {
      if (data === 'cerrado')
        body.classList.add('sidebar-collapse')
      else
        body.classList.remove('sidebar-collapse')
    }
  }
}
App.contextType = SeccionContext;
export default App;