import React from 'react'
import { Button, FormControl } from 'react-bootstrap'
import { Detalles } from '../../../Modelos/General'
import { Producto } from '../../../Modelos/Productos'
import { numberFormat } from '../../../Utiles/Formatos'
import { InputNumber } from '../../../Utiles/Inputs'

interface Props {
    producto: Producto,
    add: (iten: Detalles) => void
}
export default class FilaFactura extends React.Component<Props, Detalles>{
    constructor(p: Props) {
        super(p)
        let { costo, precio1 } = p.producto;
        this.state = {
            ...p.producto,
            unidades: 1,
            comentario: '',
            precio: parseFloat((costo * (1 + precio1 / 100)).toFixed(2))
        }
        this.setState = this.setState.bind(this);
    }
    render() {
        let { unidades, precio, descripcion, codigo, costo, precio1, precio2, precio3, cantidad, inventario } = this.state
        return <tr>
            <td>{codigo}</td>
            <td>{descripcion}</td>
            <td><InputNumber min={.01} step='0.01' setData={this.setState} value={unidades} update="unidades" /></td>
            <td><FormControl style={{ textAlign: 'right' }} disabled value={(inventario === 1) ? 'NO APLICA' : numberFormat(cantidad)} /></td>
            <td>
                <InputNumber step='0.01' list={costo + 'list'} setData={this.setState} update="precio"
                    min={(costo * (1 + precio1 / 100))} value={precio} />
                <datalist id={costo + 'list'}>
                    <option>{(costo * (1 + precio1 / 100)).toFixed(2)}</option>
                    <option>{(costo * (1 + precio2 / 100)).toFixed(2)}</option>
                    <option>{(costo * (1 + precio3 / 100)).toFixed(2)}</option>
                </datalist>
            </td>
            <td><FormControl disabled value={numberFormat((precio * unidades))} /></td>
            <td style={{ textAlign: 'right' }}>
                <Button data-tip="AGREGAR PRODUCTO" onClick={() => { this.props.add(this.state) }} variant="primary">
                    <i className="fa fa-plus" />
                </Button>
            </td>
        </tr>
    }
}