import React from "react";
import { Accordion, Button, Card, Col, InputGroup } from "react-bootstrap";
import { toast } from "react-toastify";
import { conCotizacion, conFactura, conNota, Cotizacion, Factura, sentFactura } from "../../../Modelos/Factura";
import { GeneralFila, generalNull } from "../../../Modelos/General";
import ClienteSelector from "../../Clientes/Selector";
import BuscarCotizacion from "../../Cotizacion/BuscarCotizacion";
import BuscarNota from "../../Nota/BuscarNota";
import AuxiliarFactura from "./AuxiliarFactura";
import { InputArea } from "../../../Utiles/Inputs";
import { ConConfig } from "../../../Modelos/Config";
interface State extends Factura {
    open: boolean
    cargarCotizacion: boolean
    cargarNota: boolean
    dolar: boolean
}
export default class NuevaFactura extends React.Component<any, State>{
    con = conFactura();
    impuesto = 0;
    tasa = 1;
    procesar = true;
    constructor(p: any) {
        super(p);
        this.setState = this.setState.bind(this)
        this.setCliente = this.setCliente.bind(this)
        this.state = {
            ...generalNull,
            cod_cliente: '',
            condicion: 'CONTADO',
            open: false,
            cargarCotizacion: false,
            cargarNota: false,
            dolar: false
        };
    }
    render() {
        let { state, setState, setCliente } = this;
        if (state.codigo > 0)
            return <iframe src={"/api/Factura/PDF/" + state.codigo} width='100%' height='900wh' title="print" scrolling='no' frameBorder='0' />
        return <Card>
            <BuscarCotizacion open={state.cargarCotizacion} add={this.cargarCotizacion.bind(this)} close={() => { this.setState({ cargarCotizacion: false }) }} />
            <BuscarNota open={state.cargarNota} add={this.cargarNota.bind(this)} close={() => { this.setState({ cargarNota: false }) }} />
            <Card.Header className='bg-info'>
                <h4 className='mr-auto'><i className="fa fa-edit"></i> NUEVA FACTURA</h4>
            </Card.Header>
            <Card.Body>
                <ClienteSelector Cliente={state.cod_cliente} add={setCliente} />
                <Accordion>
                    <Col xs={5}>
                        <InputGroup style={{ width: '100%' }} className="mb-3">
                            <InputGroup.Prepend><InputGroup.Text>CONDICION</InputGroup.Text></InputGroup.Prepend>
                            <select onChange={this.setCondicion.bind(this)}
                                className="form-control"
                                defaultValue={this.props.condicion}>
                                <option value="CONTADO">CONTADO</option>
                                <option value="CREDITO">CREDITO</option>
                            </select>
                        </InputGroup>
                    </Col>
                    <Accordion.Toggle eventKey='0' as={Button} variant='primary'>OBSERVACI√ìN</Accordion.Toggle>
                    <Accordion.Collapse eventKey='0'><Card>
                        <Card.Body>
                            <InputArea rows={4} maxLength={600} value={state.nota} setData={setState} update="nota" />
                        </Card.Body>
                    </Card>
                    </Accordion.Collapse>
                </Accordion>
                <AuxiliarFactura key="auxFactura" Prosesar={this.propcesar.bind(this)} setData={setState} data={state} />
            </Card.Body>
        </Card>
    }
    cargarCotizacion(d: GeneralFila) {
        conCotizacion().detalle(d.codigo, (data: Cotizacion) => {
            let d: any = { ...data };
            console.log(d);
            d.id_cotizacion = data.codigo;
            d.codigo = 0;
            d.nota = this.state.nota;
            if (data.tasa !== 0)
                d.detalles = data.detalles.map(p => {
                    if (data.tasa > 0) {
                        p.precio = p.precio * this.tasa / data.tasa;
                        p.precio = parseFloat(p.precio.toFixed(2));
                    }
                    return p;
                })

            this.setState(d)
        });
    }
    cargarNota(d: GeneralFila) {
        conNota().detalle(d.codigo, (data) => {
            data.id_nota = data.codigo;
            data.codigo = 0;
            data.nota = this.state.nota;
            this.setState(data)
        });
    }
    componentDidMount() {
        this.con.get('api/Region/get', data => {
            this.impuesto = data.impuesto;
        })
        this.dolar();
        ConConfig('costo').pull(data => {
            this.setState({ dolar: data.tasa });
        });
        this.con.get('api/Config/getFactura', data => {
            this.setState({ nota: data.observacion });
        })
        this.dolar()
    }
    async dolar() {
        try {
            let token = localStorage.getItem('Authorization');
            let response = await fetch('/api/Dolares/valor', { headers: { 'Authorization': 'Bearer ' + token } });
            let datos = await response.json();
            this.tasa = datos;
        } catch (error) {
            console.log(error);
        }
    }
    setCliente(p: string) {
        this.setState({ cod_cliente: p });
    }
    setCondicion(v: React.ChangeEvent<HTMLSelectElement>) {
        let value = v.currentTarget.value as string
        this.props.change(value)
    }
    propcesar() {
        let procesar = true;
        if (this.state.cod_cliente === "") {
            toast('DEBE SELECCIONAR UN CLIENTE', { type: toast.TYPE.ERROR });
            procesar = false;
        } else if (this.state.detalles.length === 0) {
            toast('DEBE SELECCIONAR AL MENOS UN PRODUCTO', { type: toast.TYPE.ERROR });
            procesar = false;
        }
        this.state.detalles.forEach(p => {
            if (p.unidades > p.cantidad) {
                if (p.inventario !== 1) {
                    toast('NO HAY SUFICENTE ' + p.descripcion + ' PARA COMPLETAR LA OPERACION', { type: toast.TYPE.ERROR });
                    procesar = false;
                }
            } else if (p.costo > p.precio) {
                toast(p.descripcion + ' ESTA POR DEBAJO DE SU COSTO REGISTRADO', { type: toast.TYPE.ERROR });
                procesar = false;
            }
        })
        if (this.procesar && procesar) {
            this.procesar = false;
        } else {
            toast('YA LE A DADO PROCESAR', { type: toast.TYPE.ERROR });
            procesar = false;
        }
        if (procesar)
            this.con.nuevo(sentFactura(this.state, this.impuesto),
                data => { this.setState({ codigo: data }) });

    }
}