import React from 'react';
import { Button, InputGroup, Row } from 'react-bootstrap';
import { ConConfig } from '../../Modelos/Config';
import { GeneralFila } from '../../Modelos/General';
import ModalBusqueda from '../../Utiles/Buscadores/Modal';
import { InputText } from '../../Utiles/Inputs';
import FilaGeneraModal from '../../Utiles/Prototipos/FilaGeneraModal';
interface Props {
    add: (p: GeneralFila) => void
    cargarCache: () => void
}
export default class BuscarGuardado extends ModalBusqueda<Props>{
    buscarEn = ['codigo', 'nombre', 'fecha',];

    Title() {
        return <h4 className='mr-auto'>BUSCAR GUARDADOS</h4>
    }
    Buscador() {
        return <Row style={{ paddingBottom: '15px' }}>
            <div className="col-md-9 mr-auto">
                <InputGroup>
                    <InputGroup.Prepend>
                        <InputGroup.Text>
                            {this.buscarLabel}
                        </InputGroup.Text>
                    </InputGroup.Prepend>
                    <InputText upper setData={this.setState} update='buscar' value={this.state.buscar} />
                    <InputGroup.Append>
                        <Button variant="primary">
                            <i className="fa fa-search" /> BUSCAR
                        </Button>
                    </InputGroup.Append>
                </InputGroup>
            </div>
            <Button style={{ width: '200px', }} onClick={this.props.add} variant="warning">
                <i className="fa fa-folder-open" /> DESDE CACHE
            </Button>
        </Row>
    }
    Header() {
        const { Th } = this;
        return <tr className="info">
            <Th label='codigo' width='100px'>NUMERO</Th>
            <Th label='codigo' width='90px'>FECHA</Th>
            <Th label='codigo' width='calc(100% - 585px)'>USUARIO</Th>
            <Th label='codigo' width='100px'>ESTATUS</Th>
            <Th label='codigo' width='135px'>ACCIONES</Th>
        </tr>
    }
    Row(c: GeneralFila) {
        return <FilaGeneraModal key={c.codigo + c.status} data={c} type='Cotizacion' add={this.add.bind(this)} />
    }
    add(c: GeneralFila) { this.props.add(c); this.props.close(); }
    set() { ConConfig('').get('/api/Cotizacion/plantillas', itens => { this.setState({ itens }) }); }
}