import React from 'react'
import { Button } from 'react-bootstrap'
import { Departamento } from '../../../Modelos/Productos'
import { InputNumber } from '../../../Utiles/Inputs'

interface Props {
    departamento: Departamento,
    add: (d: { precio1: number, precio2: number, precio3: number, codigo: string }) => void
}
export default class FilaUtilidad extends React.Component<Props, { precio1: number, precio2: number, precio3: number }>{
    constructor(p: Props) {
        super(p)
        this.state = { precio1: 10, precio2: 20, precio3: 30 };
        this.setState = this.setState.bind(this);
    }
    render() {
        let { state: { precio1, precio2, precio3 }, props: { departamento: { codigo, descripcion } }, setState } = this;
        return <tr>
            <td>{codigo}</td>
            <td>{descripcion}</td>
            <td><InputNumber min={.01} step='0.01' setData={setState} value={precio1} update="precio1" /></td>
            <td><InputNumber min={.01} step='0.01' setData={setState} value={precio2} update="precio2" /></td>
            <td><InputNumber min={.01} step='0.01' setData={setState} value={precio3} update="precio3" /></td>
            <td style={{ textAlign: 'right' }}>
                <Button data-tip="PLICAR A LA CATEGORIA" onClick={() => { this.props.add({ codigo, precio1, precio2, precio3 }) }} variant="primary">
                    <i className="fa fa-plus" />
                </Button>
            </td>
        </tr>
    }
}