import React from 'react';
import { Detalles } from '../../../Modelos/General';
import { conDepartamento, Departamento, conProducto, Producto } from '../../../Modelos/Productos';
import FilaPrecio from './FilaPrecio';
import FilaUtilidad from './FilaUtilidad';
import ModalBusqueda from '../../../Utiles/Buscadores/Modal';
interface Props {
    type: 'COSTO' | 'UTILIDAD';
    add: (p: Detalles[]) => void
}
export default class CategoriaBuscar extends ModalBusqueda<Props> {
    con = conDepartamento();
    conP = conProducto();
    productos: Producto[] = [];
    buscarEn = ['codigo', 'descripcion'];
    tasa = 1;
    Title() { return <h4 className='mr-auto'>BUSCAR PRODUCTOS</h4> }
    Header() {
        const { Th } = this;
        return <tr className="info">
            <Th label='codigo' width='100px'>CODIGO</Th>
            <Th label='descripcion' width='auto'>DESCRIPCION</Th>
            {this.head()}
            <Th width='40px'> </Th>
        </tr>
    }
    Row(c: Departamento) {
        switch (this.props.type) {
            case 'COSTO':
                return <FilaPrecio key={c.codigo + 'COSTO'} departamento={c} add={this.addPrecio.bind(this)} />;
            case 'UTILIDAD':
                return <FilaUtilidad key={c.codigo + 'UTILIDAD'} departamento={c} add={this.addUtilidad.bind(this)} />;
        }
    }
    filter() {
        let itens = this.ordenar();
        return itens.filter((c: Producto) => {
            if (c.estatus === 0) return false;
            return true;
        });
    }
    head() {
        const { Th } = this;
        switch (this.props.type) {
            case 'COSTO':
                return <>
                    <Th width='150px'>OPERACION</Th>
                    <Th width='300px'>COSTO</Th>
                </>;
            case 'UTILIDAD':
                return <>
                    <Th width='150px'>PRECIO 1</Th>
                    <Th width='150px'>PRECIO 2</Th>
                    <Th width='150px'>PRECIO 3</Th>
                </>;
        }
    }
    set() {
        this.con.cargarListaWeb((itens: Departamento[]) => {
            itens.push({
                codigo: 'TODO',
                descripcion: 'TODOS LO PRODUCTOS EN EL INVENTARIO',
                estatus: 1
            })
            this.setState({ itens })
        });
        this.conP.cargarListaWeb((itens) => { this.productos = itens });
    }
    addPrecio({ codigo, precio, operacion }: { codigo: string, precio: number, operacion: string }) {
        let d: Detalles[] = [];
        this.productos.forEach((c: Producto) => {
            if (codigo === 'TODO' || c.departamento === codigo) {
                let costo = c.costo;
                switch (operacion) {
                    case 'mas': costo += precio; break;
                    case 'menos': costo -= precio; break;
                    case 'por': costo *= precio; break;
                    case 'entre': costo /= precio; break;
                }
                let k: Detalles = { ...c, precio: costo, unidades: 1, comentario: '' };
                d.push(k);
            }
        });
        this.props.add(d);
    }
    addUtilidad({ codigo, precio1, precio2, precio3 }: { codigo: string, precio1: number, precio2: number, precio3: number }) {
        let d: Detalles[] = [];
        this.productos.forEach((c: Producto) => {
            if (codigo === 'TODO' || c.departamento === codigo) {
                let k: Detalles = { ...c, precio: c.costo, unidades: 1, comentario: '' };
                k.precio1 = precio1;
                k.precio2 = precio2;
                k.precio3 = precio3;
                d.push(k);
            };
        });
        this.props.add(d);
    }

}