import React from 'react'
import { Button } from 'react-bootstrap'
import { Detalles } from '../../../Modelos/General'
import { Producto } from '../../../Modelos/Productos'
import { InputNumber } from '../../../Utiles/Inputs'

interface Props {
    producto: Producto,
    add: (iten: Detalles) => void
    tasa: number
}
export default class FilaDolar extends React.Component<Props, Detalles>{
    constructor(p: Props) {
        super(p)
        let { costo, precio2 } = p.producto;
        this.state = {
            ...p.producto,
            unidades: 1,
            comentario: '',
            precio: parseFloat((costo * (1 + precio2 / 100)).toFixed(2))
        }
        this.setState = this.setState.bind(this)
    }
    render() {
        let { unidades, precio, descripcion, codigo, costo, precio1, precio2, precio3 } = this.state;
        let costoDolar = costo / this.props.tasa,
            precioDolar = parseFloat((precio / this.props.tasa).toFixed(2)),
            precio1Dolar = parseFloat((costoDolar * (1 + precio1 / 100)).toFixed(2)),
            precio2Dolar = parseFloat((costoDolar * (1 + precio2 / 100)).toFixed(2)),
            precio3Dolar = parseFloat((costoDolar * (1 + precio3 / 100)).toFixed(2));
        return <tr>
            <td>{codigo}</td>
            <td>{descripcion}</td>
            <td><InputNumber min={.01} step='.01' setData={this.setState} value={unidades} update="unidades" /></td>
            <td>
                <InputNumber step='0.01' list={codigo + 'list'}
                    min={parseFloat((costoDolar * (1 + precio1 / 100)).toFixed(2))}
                    setData={this.setPrecio.bind(this)} value={precioDolar} update="precio" />
                <datalist id={codigo + 'list'}>
                    <option>{precio1Dolar}</option>
                    <option>{precio2Dolar}</option>
                    <option>{precio3Dolar}</option>
                </datalist>
            </td>
            <td><InputNumber disabled setData={() => { }} value={precio * unidades / this.props.tasa} update="p" /></td>
            <td style={{ textAlign: 'right' }}>
                <Button data-tip="AGREGAR PRODUCTO" onClick={() => { this.props.add(this.state) }} variant="primary">
                    <i className="fa fa-plus" />
                </Button>
            </td>
        </tr>
    }
    setPrecio(data: { precio: number }) {
        this.setState({ precio: data.precio * this.props.tasa });
    }
}